* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  height: 100%;
  box-sizing: border-box;
  width: 100%;
}

body {
  background-image: url("https://images4.imagebam.com/43/05/00/MEGJFLN_o.jpg");
  background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
} 
 

.stretch {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

.footer2 {
  text-align: center;
 
  
  gap: 5px;
  list-style-type: none;
  color: white ;
   
  padding-top: 1rem;
  font-family: 'Courier New', Courier, monospace;
  font-size: 0.8rem;
  margin-top: -0.5rem;
}
 
#footer-wrapper2 {
 
  padding-top: 1rem;
  padding-bottom: 1rem;
  width: 100%;
  margin-top: 2rem;
  position: fixed;
  left: 0%;
  bottom: 0px;
  background: rgba(1,1,1, 50%);
